@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Open+Sans&family=Roboto&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat&family=Open+Sans&family=Roboto&display=swap');
:root{
    --rofof-font : 'Roboto', sans-serif;
    --OpenSansf-font : 'Open Sans', sans-serif; 
    --monof-font :  'Montserrat', sans-serif;
}
.newscards-container{
    /* border: 3px solid red; */
    width: auto;
    padding: 2rem;
}

.card-container{
    display: flex;
     justify-content: center;
    flex-wrap: wrap;
    gap: 1rem;
    /* border: 3px solid red; */
    padding: 2rem;
    margin-top: 2rem;
    color: white;
}
.card-container > * {
    flex-basis: 40%;
    /* border: 3px solid red; */
    border-radius: 5px;
    background: #130f40;
    padding: 4rem 3rem;
}
.cards img{
    width: 100%;
    height: auto;
}
.news-title{
    font-size: 1.3rem;
    margin-top: 2rem;
    font-family: var(--monof-font);
    letter-spacing: 0.1rem;
}
.news-desc{
    font-size: 1.1rem;
    margin-top: 2rem;
    color:darkgray;
}
.news-button{
    padding: 1rem 2rem;
    outline: none;
    border: transparent;
    font-size: 1.2rem;
    margin-top: 2rem;
    background: #5f27cd;
    color: white;
    transition: .3s ease-in;
    cursor: pointer;
    border: 1px solid #5f27cd;
    box-shadow: 0px 2px 3px #140e0e94;
}
.news-button:hover{
    opacity: 0.8;
}
@media screen and (max-width: 40rem) {
    .card-container{
        padding: 2rem 1rem;
        margin-top: 0rem;
        gap: 2rem;
    }
    
    .card-container > *{
        flex-basis: 100%;
        padding: 2rem 1rem;
        background: #ffffff;
        box-shadow: 0px 2px 6px #808080 ;
    }
    .newscards-container{
        padding: 0rem;
    }
    .newscards-container h1{
        padding: 0.3rem 1rem;
        padding-top: 2rem;
        text-align: center;
    }
    .cards img{
        width: 100%;
    }
    .news-title{
        font-size: 1.1rem;
        margin-top: 1rem;
        color: black;
    }
    .news-desc{
        margin-top: 1rem;
    }
    .news-button{
        font-size: 1rem;
    }
}