@import url('https://fonts.googleapis.com/css2?family=PT+Sans&display=swap');
:root{
  --accent-font:  'PT Sans', sans-serif;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  text-align: center;
  background: #dff9fb;
}
h1{
  font-family: var(--accent-font);
  font-weight: bold;
}
@media screen and (max-width: 40rem) {
  body{
    text-align: left;
  }
}
