.header{
 background: #20232a;
 color: white;
 padding: 2rem 4rem;
 letter-spacing: 0.2rem;
 display: flex;
 justify-content: center;
 align-items: center;
}
.header img{
    width: 80px;
    height: auto;
    margin-right: 1rem;
}

@media screen and (max-width: 40rem) {
    .header{
        justify-content: flex-start;
        padding: 2rem 1rem;
        text-align: left;
    }
    .header img{
        width: 3rem;
    }
    .header h1{
        font-size: 1.5rem;
    }
    
}